.header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    padding: 5% 0;
    -ms-flex-pack: center;
        justify-content: center;
    background-image: url('../../header-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.header-item{
    background-color: rgba(255,255,255,0.25);
    color: #263238;
    padding: 10px;
}

.icon-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: center;
        align-content: center;
    margin-top: 30px;
}

.tech-icon{
    width: 15%;
    margin: 10px 5%;
}

.tech-icon span{
    position: relative;
    top: 10px;
    font-size: 14px;
    background-color: rgba(255,255,255,0.25);
    color: #263238;
    padding: 3px;
    word-wrap: break-word;
}

@media (min-width: 768px){
    .tech-icon{
        width: 10%;
        margin: 0;
        max-width: 80px;
    }
    .tech-icon svg {
        -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
        -webkit-transition: -webkit-transform .2s,-webkit-filter .2s;
        transition: -webkit-transform .2s,-webkit-filter .2s;
        -o-transition: transform .2s,filter .2s;
        transition: transform .2s,filter .2s;
        transition: transform .2s,filter .2s,-webkit-transform .2s,-webkit-filter .2s;
    }
    .tech-icon svg:hover {
        -webkit-filter: grayscale(0);
                filter: grayscale(0);
        -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
                transform: scale(1.1);
    }

    .tech-icon span{
        opacity: 0;
        -webkit-transition: opacity .2s;
        -o-transition: opacity .2s;
        transition: opacity .2s;
    }

    .tech-icon:hover span{
        opacity:1;
    }
}



