.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.intro, .item-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-align: left;
  margin: 30px 0;
}
.intro{
  -ms-flex-align: center;
      align-items: center;
}
.intro > div, .home-item {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  padding: 10px;
}
.intro > .intro-text{
  -ms-flex-positive: 2;
      flex-grow: 2;
}
.home-item h3{
  text-align: center;
  text-transform: uppercase;
}

img{
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}

@media (max-width: 768px){
  .intro > div, .home-item {
    -ms-flex-preferred-size: 80%;
        flex-basis: 80%;
  }
}




