.about{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.about-r{
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 2;
        flex-grow: 2;
    padding: 10px;
    text-align: left;
}

.about-l{
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 10px;
}
.card{
  margin: 10px 0;  
}
.about h3 {
    color: #0086c3;
    text-align: center;
}
.about a {
    color: #0086c3;
    text-decoration: none;
}
.info-element{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border-bottom: 1px solid gray;
}

.info-element > p {
    width: 50%;
    word-wrap: break-word;
    text-align: right;
    padding: 0 3px;
}

.value, .about li, .tech-info p{
    text-align: left !important;
}

@media (max-width: 768px){
    .about-r, .about-l{
        -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
    }
}


